.header {
  width: 100%;
  padding: 0 var(--side-offset, 20px);
  padding-top: 30px;
  position: absolute;
  z-index: 2;

  @media (max-width: 1199px) {
    padding-top: 34px;
  }

  @media (max-width: 767px) {
    padding-top: 16px;
  }
}

.header__row {
  display: flex;
  justify-content: space-between;
}

.header__logo svg {
  display: block;
  margin-top: 10px;
  color: #ffffff;
  z-index: 10;
  transition: all 0.2s ease-out;
  cursor: pointer;

  @media (max-width: 1199px) {
    margin-top: 8px;
    width: 81px;
    height: 16px;
  }

  @media (max-width: 767px) {
    margin-top: 6px;
    width: 67px;
    height: 13px;
  }

  &:hover {
    color: #868686;
  }
}

.header__phone {
  display: flex;
  font-family: $ff-accent;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
  transition: all 0.2s ease-out;
  z-index: 10;

  @media (max-width: 1199px) {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    align-items: center;
    letter-spacing: 0.02em;
  }

  @media (max-width: 767px) {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }

  &:hover {
    color: #868686;
  }
}
